@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
  --blue-color: #2e313e;
}

.page_renderer {
  min-height: 60vh;
}

.auto-fill-bg:-webkit-autofill,
.auto-fill-bg:-webkit-autofill:hover,
.auto-fill-bg:-webkit-autofill:focus,
.auto-fill-bg:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px #2e313e inset !important;
  -webkit-box-shadow: 0 0 0 30px var(--blue-color) inset !important;
  -webkit-text-fill-color: #fff !important;
}

input[type="number"] {
  -moz-appearance: textfield;
}

.input-type-number::-webkit-outer-spin-button,
.input-type-number::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
.bg-blue {
  background-color: #2e313e;
  background-color: var(--blue-color);
}
.font-size-1rem {
  font-size: 1rem;
}
.font-weight-700 {
  font-weight: 700;
}
.c-pointer {
  cursor: pointer;
}
.flex-wrap {
  flex-wrap: wrap;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
button {
  padding: 0;
}
ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  padding: 0;
  margin: 0;
}
a,
a:hover,
a:focus {
  color: inherit;
  text-decoration: none;
}

